@font-face {
    font-family: 'AdobeClean';
    src: local('AdobeClean'), url(./AdobeClean-Regular.ttf) format('truetype');
  }

.header {
    font-family: AdobeClean;
}

.hexagonRow {
    display: inline-block;
    width: 150px;
    height: 150px;
}

h2 {
    font-family: "Arial", sans-serif;
    text-align: center;
    font-size: 24px;
    margin-bottom: 30px;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: black;
  }

h3 {
    font-family: "Arial", sans-serif;
    text-align: center;
    font-size: 20px;
    padding-top: 5px;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: black;
  }

.hexagon {
    display: inline;
}